html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
}

/* utilities */

.footer {
  text-align: center;
  color: #fff4e6;
  color: var(--light);
  min-height: 20vh;
}

.card-shadow {
  border-radius: 10px;
  background: #343a40;
  box-shadow: 40px 40px 64px #292d32, -40px -40px 64px #3f474e;
}

.btn-shadow {
  border-radius: 22px !important;
  background: #343a40;
  box-shadow: 12px 12px 24px #25292d, -12px -12px 24px #434b53;
}

.btn-shadow:hover {
  border: none;
  border-radius: 22px !important;
  background: #343a40;
  box-shadow: inset 12px 12px 24px #25292d, inset -12px -12px 24px #434b53;
  text-decoration: none !important;
}

.btn-shadow-sm {
  color: #a2a8ad !important;
  border: none !important;
  border-radius: 50px !important;
  background: linear-gradient(145deg, #1b1e21, #17191c);
  box-shadow: 7px 7px 15px #111315, -7px -7px 15px #212529;
}

.btn-shadow-sm:hover {
  color: #ffffff !important;
  border-radius: 50px;
  background: #191c1f;
  box-shadow: inset 7px 7px 15px #111315, inset -7px -7px 15px #212529;
}

Section {
  padding-bottom: 10vh;
}

.about-img img {
  border-radius: 22px;
  padding: 20px 0 20px 0;
  height: 100%;
  width: 100%;
}

.sign {
  position: relative;
  z-index: 1;
  height: 3.5vh;
  padding: 0px;
  margin: 0px;
  border-top-width: 12px;
  border-top-style: solid;
  border-bottom-width: 7px;
  border-bottom-style: solid;
  border-image: linear-gradient(
      180deg,
      hsl(180, 2%, 18%) 0%,
      hsl(0, 0%, 83%) 35%,
      hsl(0, 0%, 52%) 47%,
      hsl(240, 1%, 29%) 100%
    )
    100% 1;
  box-shadow: 0 5px 25px 20px #2c2e30;
}

.contact-card img {
  height: 30vh;
}

.fill-screen {
  min-height: 94vh;
}

.btn-darker {
  background-color: #000000;
}

.back-dark {
  background-color: rgba(52, 58, 64);
}

.back-dark::after {
  background-image: url(data:image/png;base64,);
}

.bottom-border {
  border-bottom: solid 3px #ff001f;
  border-bottom: solid 3px var(--crimson);
}

.nav-link {
  margin: 0 3px 0 3px;
}

.bigger-bar {
  min-height: 6vh;
  border-bottom: solid #ff001f;
  border-bottom: solid var(--crimson);
}

.dark-bar {
  background-color: #191c1f;
}

.contact-section {
  color: white;
}

.big-neon {
  font-family: "Neon W05 Display";
  font-weight: normal;
  font-size: 2.5vh;
  border: solid 4px;
  border-radius: 25px;
  padding: 5px 20px 5px 20px;
  color: #979da0;
}

.big-neon:hover {
  color: #d8c5bd;
  text-shadow: 4px 4px 35px #ff001f, -4px -4px 35px #ff001f;
  text-shadow: 4px 4px 35px var(--crimson), -4px -4px 35px var(--crimson);
  box-shadow: 4px 4px 15px #ff001f, 4px 4px 15px #ff001f inset;
  box-shadow: 4px 4px 15px var(--crimson), 4px 4px 15px var(--crimson) inset;
  text-decoration: none;
}

.neon {
  font-family: "Neon W05 Display";
  font-weight: normal;
  border: solid 3px;
  border-radius: 15px;
  padding: 5px;
  color: #d8c5bd;
}

.project-btn {
  height: 5vh;
  width: 5vh;
}

.carousel-project {
  height: 100%;
  width: 100%;
}

.no-glow-neon {
  font-family: "Neon W05 Display";
  font-weight: normal;
  border: solid 3px;
  border-radius: 15px;
  padding: 5px;
  color: #d8c5bd;
}

.neon:hover {
  color: #d8c5bd;
  text-shadow: 4px 4px 50px #ff001f, -4px -4px 50px #ff001f;
  text-shadow: 4px 4px 50px var(--crimson), -4px -4px 50px var(--crimson);
  box-shadow: 4px 4px 50px #ff001f, -4px -4px 50px #ff001f;
  box-shadow: 4px 4px 50px var(--crimson), -4px -4px 50px var(--crimson);
  text-decoration: none;
}

main {
  box-shadow: 0 -50px 20px #2b2f34 inset;
}

.glowing {
  position: relative;
  z-index: 2;
  color: #d3b7ac;
  text-shadow: 4px 4px 15px #ff001f, -4px -4px 15px #ff001f;
  text-shadow: 4px 4px 15px var(--crimson), -4px -4px 15px var(--crimson);
  box-shadow: 4px 4px 35px 0 #ff001f, -4px -4px 35px 0 #ff001f,
    4px 4px 35px #ff001f inset, -4px -4px 35px #ff001f inset;
  box-shadow: 4px 4px 35px 0 var(--crimson), -4px -4px 35px 0 var(--crimson),
    4px 4px 35px var(--crimson) inset, -4px -4px 35px var(--crimson) inset;
}

.glowing-less {
  color: #d8c5bd;
  text-shadow: 3px 3px 6px #ff001f, -3px -3px 6px #ff001f;
  text-shadow: 3px 3px 6px var(--crimson), -3px -3px 6px var(--crimson);
  box-shadow: 3px 3px 20px #ff001f, -3px -3px 20px #ff001f,
    3px 3px 20px #ff001f inset, -3px -3px 20px #ff001f inset;
  box-shadow: 3px 3px 20px var(--crimson), -3px -3px 20px var(--crimson),
    3px 3px 20px var(--crimson) inset, -3px -3px 20px var(--crimson) inset;
}

.subtitle {
  background-color: rgb(253, 200, 27);
  background-color: var(--gold);
  color: #171717;
  color: var(--black);
  font-size: 6vh;
  font-weight: bold;
  margin: 0;
  padding: 10px 1em;
  line-height: 90%;
  border-radius: 0 0 0 15px;
}

.subtitle-small {
  font-size: 15px;
}

/* end utilities */

/* header section */
.Typewriter {
  color: white;
  font-size: 50px;
  opacity: 1;
}

h1 a {
  font-size: 5vh;
}

header {
  background-color: #171717;
  background-color: var(--black);
  color: #ff001f;
  color: var(--crimson);
  height: 6vh;
}

.jumbo {
  background-image: url(/portfolio/static/media/street.345f69e8.jpg);
  background-size: cover;
  background-attachment: scroll;
  background-position: 17% 75%;
  height: 94vh;
  border-bottom: solid 3px #ff001f;
  border-bottom: solid 3px var(--crimson);
  box-shadow: 0 50px 20px #2b2f34;
}

.typewriter-container {
  color: white;
  margin-top: 20vh;
}

.section-title {
  font-family: "Neon W05 Display";
  text-align: center;
  border: solid 8px;
  border-radius: 15px;
  font-size: 2em;
}

.flicker-1 {
  -webkit-animation-name: flicker-1;
          animation-name: flicker-1;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}

.flicker-2 {
  -webkit-animation-name: flicker-2;
          animation-name: flicker-2;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-iteration-count: 3;
          animation-iteration-count: 3;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.flicker-3 {
  -webkit-animation-name: flicker-3;
          animation-name: flicker-3;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-delay: 8s;
          animation-delay: 8s;
}

.flicker-4 {
  -webkit-animation-name: flicker-4;
          animation-name: flicker-4;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-delay: 8s;
          animation-delay: 8s;
}

/* media queries */
@media screen and (max-width: 900px) {
  .big-neon {
    font-size: 25px;
    border: solid 2px;
  }

  .subtitle {
    font-size: 20px;
  }
}

